body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.container {
  background-color: white;
  margin: auto;
  padding-left: 20px;
  padding: 20px;
  /* height: 100vh; Magic here */
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  margin: 0 auto;
  float: none;
  margin-bottom: 20px;
  width: 100%;
}

.jumbotron {
  margin-bottom: 40px;
  padding-bottom: 10px;
  margin: auto;
  padding-left: 20px;
  padding: 10px;
}

h1 {
  /* padding-bottom: 5px; */
  margin: auto;
  font-family: "Arizonia", cursive;
  font-size: 120px;
}

.navbar-nav {
  float: none;
  margin: 0 auto;
  text-align: center;
  font-size: 14px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

